import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import $api from '../http';
import DailyIframe from '@daily-co/daily-js';

export default function Call() {
  const { id } = useParams();
  console.log(id);
  useEffect(() => {
    const domain = 'https://bhrushetskyi.daily.co/';
    $api
      .get(`/video-call/${id}`)
      .then(res => {
        const script = document.createElement('script');
        script.innerHTML = DailyIframe.createFrame({
          iframeStyle: {
            position: "absolute",
            width: "90vw",
            height: "90vh",
            border: "0",
            top: "90px",
            left: "5%",
            zIndex: 5
          },
          showLeaveButton: true,
          showFullscreenButton: true,
        }).join({ url: `${domain}${id}` });

      })
      .catch(err => console.log(err));
  }, [id]);


  return <div style={{ height: '91vh', width: '91vw' }} ></div>;
  ;
}
